const env = process.env.NODE_ENV;

const port = 22222

const ip = require('../../config/host')

const host = ip.ip()

const production = {
  all: "http://"+host+":"+port,
};
const development = {
  all: "http://"+host+":"+port,
};

export default env === 'production' ? production : development;



