let os = require('os')

let ip = ''
module.exports = {
  ip() {
    try {
      const network = os.networkInterfaces()
      network[Object.keys(network)[0]].forEach(function (details) {
        if (ip === '' && details.family === 'IPv4' && !details.internal) {
          ip = details.address
          return ip
        }
      })
    } catch (e) {
      console.log(124)
      ip = '127.0.0.1'
    }
    return ip
  }
}





